* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --yellow: #ffed10;
  --red: #e20025;
  --blue: #0060AA;
}

body {
  width: 100%;
  min-height: 100vh;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  font-family: 'Bangers', cursive;
}

@media (orientation: landscape) {
  body {
    background-image: url(/superheroChallenges/static/media/bg1.b486726a.jpg);
  }
}

@media (min-width: 641px) and (orientation: landscape) {
  body {
    background-image: url(/superheroChallenges/static/media/bg2.55e70ee8.jpg);
  }
}

@media (min-width: 1281px) and (orientation: landscape) {
  body {
    background-image: url(/superheroChallenges/static/media/bg3.6c2756dd.jpg);
  }
}

@media (orientation: portrait) {
  body {
    background-image: url(/superheroChallenges/static/media/bg2vert.489778c9.jpg);
  }
}
@media (min-height: 641px) and (orientation: portrait) {
  body {
    background-image: url(/superheroChallenges/static/media/bg3vert.56f606ac.jpg);
  }
}
.appWrap {
    width: 96%;
    min-height: 92vh;
    margin: 4vh auto 4vh;
    border-radius: 2vw;
    max-width: 900px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

main {
-webkit-flex-grow: 1;
        flex-grow: 1;
padding: 2vw;
}

@media (orientation: portrait) {
    .appWrap {
        width: 92%;
        min-height: 96vh;
        margin: 2vh auto 2vh;
    }
}
header h1 {
    text-align: center;
    letter-spacing: 5px;
    line-height: 1.3;
    font-size: 6vw;
    background-color: var(--yellow);
    padding: 3vw;
    margin: 1vw 0;
    border-radius: 2vw;
    box-shadow: 0 0 3vw var(--yellow)
}

header ul {
    list-style: none;
    color: #010101;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    margin-top: 2vw;
    background-color: rgba(0,0,0,.3);
    border-radius: 2vw;
    border: 3px solid var(--red);
    box-shadow: 0 0 3vw inset var(--red), 0 0 2vw black;
}

header ul li {
    margin: 2vw;
    font-size: 4vw;
    letter-spacing: 2px;
    cursor: pointer;
}
header ul li a {
    display: block;
    text-decoration: none;
    color: #010101;
    border-radius: 1vw;
    padding: 1.6vw 4vw;
}

header ul li a {
    background-color: var(--blue);
    box-shadow: 0 0 3vw var(--blue);
    -webkit-transition: .3s;
    transition: .3s;
}

header ul li:hover a {
    background-color: var(--yellow);
    box-shadow: 0 0 3vw var(--yellow)
}

header ul li a.active {
    background-color: var(--yellow);
    box-shadow: 0 0 3vw var(--yellow);
}

@media (orientation: portrait) {
    header h1 {
        font-size: 10vw;
    }

    header ul li {
        font-size: 7vw;
    }

    header ul li a {
    padding: 3vw 6vw;
    }
}

@media (min-width: 1024px) {
    header h1 {
        font-size: 50px;
    }

    header ul li {
        font-size: 36px;
    }

    header ul li a {
    padding: 2vw 4vw;
    }
}

@media (orientation: portrait) and (min-height: 1024px) {
    header h1 {
        font-size: 7vw;
    }

    header ul li {
        font-size: 5vw;
    }
}
form {
    border-radius: 2vw;
    padding: 2vw;
    margin: 2vw 1vw;
    background-color: rgba(0,0,0,.3);
    border: 3px solid var(--blue);
    box-shadow: 0 0 3vw inset var(--blue), 0 0 2vw black;
}

form h2, .inputs{
    background-color: #010101;
    color: var(--yellow);
    border-radius: 2vw;
    padding: 4vw 2vw;
    margin: 2vw;
    text-align: center;
    letter-spacing: 4px;
    font-size: 5vw;
    box-shadow: 0 0 3vw #010101;
}

.inputs {
    background-color: #fff;
    color: #010101;
    letter-spacing: 2px;
    font-size: 4vw;
    box-shadow: 0 0 3vw #fff;
}

.row {
    margin: 1vw;
    padding: 1vw;
}

input[type='text'], input[type='checkbox'], label, input[type='date'],button {
    cursor: pointer;
}

input[type='text'] {
    width: 90%;
    font-family: 'Bangers', cursive;
    font-size: 4vw;
    border: none;
    border-bottom: 1px solid var(--red);
    border-top-left-radius: 1vw;
    border-top-right-radius: 1vw;
    outline: none;
    padding: 1vw;
    -webkit-transition: .3s;
    transition: .3s;
}

input[type='text']::-webkit-input-placeholder {
color: #010101;
letter-spacing: 2px;
}

input[type='text']::-ms-input-placeholder {
color: #010101;
letter-spacing: 2px;
}

input[type='text']::placeholder {
color: #010101;
letter-spacing: 2px;
}

input[type='text']:hover {
    background-color: #ccc;
    -webkit-transition: .3s;
    transition: .3s;
}

input[type='text']:focus {
    background-image: -webkit-linear-gradient(white 95%, var(--red) 95%);
    background-image: linear-gradient(white 95%, var(--red) 95%);
}

input[type='checkbox'] {
    position: relative;
    width: 6vw;
    height: 3vw;
    -webkit-appearance: none;
    background: #c6c6c6;
    outline: none;
    border-radius: 1.5vw;
    box-shadow: inset 0 0 .3vw rgba(0,0,0,.2);
    -webkit-transition: .4s;
    transition: .4s;
}

input:checked[type='checkbox'] {
background-color: var(--red);
}

input[type='checkbox']::before {
    content: '';
    position: absolute;
    width: 3vw;
    height: 3vw;
    border-radius: 1.4vw;
    top: 0;
    left: 0;
    background-color:#ccc;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    box-shadow: 0 .5vw 1vw rgba(0,0,0,.2);
    -webkit-transition: .4s;
    transition: .4s;
}

input:checked[type='checkbox']::before {
    left: 3vw;
}

input[type='checkbox'] + label {
margin-left: 3vw;
}

input[type='date'] {
    border: none;
    font-family: 'Bangers', cursive;
    font-size: 3vw;
    letter-spacing: 2px;
    outline: none;
    margin-left: 2vw;
    background-image: -webkit-linear-gradient(white 0%, white 80%, black 80%, black 85%, white 85%);
    background-image: linear-gradient(white 0%, white 80%, black 80%, black 85%, white 85%);
    text-align: center;
}

form button {
    font-family: 'Bangers', cursive;
    font-size: 4vw;
    letter-spacing: 1px;
    outline: none;
    padding: 1vw 4vw;
    margin-top: 1vw;
    letter-spacing: 2px;
    background-color: #fff;
    border: 2px solid var(--red);
    -webkit-transition: .3s;
    transition: .3s;
    border-radius: 1vw;
    box-shadow: 0 .4vw 1vw gray;
}

form button:hover {
background-color: var(--red);
color: #fff;
}

@media (orientation: portrait) { 
    form h2{
        font-size: 7vw;
    }
    
    .inputs {
        font-size: 6vw;
    }
    
    .row {
        margin: 2vw;
        padding: 2vw;
    }
    
    input[type='text'] {
        font-size: 5vw;
    }
    
    input[type='checkbox'] {
        width: 9vw;
        height: 4.5vw;
        border-radius: 4.5vw;
    }
    
    input[type='checkbox']::before {
        width: 4.5vw;
        height: 4.5vw;
        border-radius: 2.25vw;

    }
    
    input:checked[type='checkbox']::before {
        left: 4.5vw;
    }
    
    input[type='date'] {
        font-size: 5vw;
    }
    
    form button {
        font-size: 6vw;
        padding: 2vw 5vw;
    }
}

@media (min-width: 1024px) {
    form h2{
        font-size: 45px;
    }
    
    .inputs {
        font-size: 36px;
    }
    
    .row {
        margin: 1vw;
        padding: 1vw;
    }
    
    input[type='text'] {
        font-size: 36px;
    }
    
    input[type='checkbox'] {
        width: 60px;
        height: 30px;
        border-radius: 30px;
    }
    
    input[type='checkbox']::before {
        width: 30px;
        height: 30px;
        border-radius: 15px;

    }
    
    input:checked[type='checkbox']::before {
        left: 30px;
    }
    
    input[type='date'] {
        font-size: 36px;
    }
    
    form button {
        font-size: 45px;
        padding: 1vw 4vw;
    }
}

@media (orientation: portrait) and (min-height: 1024px) {
    form h2{
        font-size: 7vw;
    }
    
    .inputs {
        font-size: 5vw;
    }
    
    input[type='text'] {
        font-size: 5vw
    }
    
    input[type='checkbox'] {
        width: 9vw;
        height: 4.5vw;
        border-radius: 4.5vw;
    }
    
    input[type='checkbox']::before {
        width: 4.5vw;
        height: 4.5vw;
        border-radius: 2.25vw;

    }
    
    input:checked[type='checkbox']::before {
        left: 4.5vw;
    }
    
    input[type='date'] {
        font-size: 5vw;
    }
    
    form button {
        font-size: 7vw;
    }
}
.task {
    background-color: white;
    border-radius: 2vw;
    padding: 2vw 3vw;
    margin: 2vw;
    box-shadow: 0 0 3vw #fff;
}

.task .row {
    font-size: 4vw;
    letter-spacing: 2px;
    text-align: center;
    line-height: 1.2;
}

.task .row:nth-child(2) {
    font-size: 3.5vw;
}

.unfinished .buttons {
display: -webkit-flex;
display: flex;
-webkit-justify-content: space-around;
        justify-content: space-around;
padding: 2vw 4vw;
}

.task button {
    padding: 1vw 4vw;
    font-family: 'Bangers', cursive;
    letter-spacing: 1px;
    font-size: 4vw;
    background-color: #fff;
    border: 2px solid #010101;
    border-radius: 1vw;
    -webkit-transition: .3s;
    transition: .3s;
    outline: none;
}

.unfinished button:nth-child(1):hover {
    border: 2px solid var(--yellow);
    background-color: var(--yellow);
}

.unfinished button:nth-child(2):hover {
    border: 2px solid var(--blue);
    background-color: var(--blue);
}

.finished button {
display: block;
margin: 0 auto;
}

.finished button:hover {
    border: 2px solid var(--red);
    background-color: var(--red);
}

@media (orientation: portrait) { 
    .task .row {
        font-size: 6vw;
    }
    
    .task .row:nth-child(2) {
        font-size: 5vw;
    }
    
    .unfinished .buttons {
    padding: 4vw 6vw;
    }
    
    .task button {
        padding: 2vw 6vw;
        font-size: 6vw;
    }
}

@media (min-width: 1024px) {
    .task .row {
        font-size: 30px;
    }
    
    .task .row:nth-child(2) {
        font-size: 25px;
    }
    
    .unfinished .buttons {
    padding: 2vw 4vw;
    }
    
    .task button {
        padding: 1vw 3vw;
        font-size: 30px;
    }
}

@media (orientation: portrait) and (min-height: 1024px) {
    .task .row {
        font-size: 5vw;
    }
    
    .task .row:nth-child(2) {
