footer {
    text-align: center;
    background-color: var(--yellow);
    border-radius: 2vw;
    box-shadow: 0 0 3vw var(--yellow);
}

footer h3 {
    line-height: 1.8;
    letter-spacing: 3px;
    font-size: 3.5vw;
}

footer a {
    color: #010101;
    text-decoration: none;
    position: relative;
    overflow: hidden;
}

footer a::before, footer a::after {
    content: '';
    position: absolute;
    bottom: -3px;
    left: 0;
    width: 100%;
    height: 3px;
    background-color: #010101;
    -webkit-transition: .3s;
    transition: .3s;
}

footer a::after {
    left: -150%;
    opacity: 0;
}

footer a:hover::before {
    left: 150%;
    opacity: 0;
}

footer a:hover::after {
    left: 0;
    opacity: 1;
}

@media (orientation: portrait) {
    footer h3 {
        line-height: 2;
        font-size: 5.4vw;
    }
}

@media (min-width: 1024px) {
    footer h3 {
        font-size: 36px;
    }
}
